import React from 'react'
import './App.css'
import { Switch, Route, useHistory } from 'react-router-dom'
import ExclusiveEdition from './components/ExclusiveEdition'
import Navbar from './components/Navbar'
import Main from './components/Main'
import LoadingScreen from './components/LoadingScreen'
import Expansion from './components/Expansion'

const App = () => {

  const history = useHistory()

  const setView = (view) => {
    history.push(`/${view}`)
  }

  const views = {}

  views['main'] = <Main />

  views['exclusiveEdition'] = <ExclusiveEdition />

  views['expansion'] = <Expansion />

  return (
    <div className="App">
      <Navbar setBody={setView} />
      <Switch>
        <Route exact path='/'>
          {views['main']}
        </Route>

        <Route exact path='/main'>
          {views['main']}
        </Route>

        <Route exact path='/exlusiveEdition'>
          {views['exclusiveEdition']}
        </Route>

        <Route exact path='/expansion'>
          {views['expansion']}
        </Route>
      </Switch>
    </div>
  )

}

export default App
